import React from "react";
import { useTiles } from "../TileHooks";
import PropTypes from "prop-types";
import "./colorMenu.css";

const Color = ({ color, isSelected }) => {
  const { selectColor } = useTiles();
  const divStyle = {
    backgroundColor: color,
    width: "20px",
    height: "20px",
    cursor: "pointer",
    paddingLeft: "2px",
    marginLeft: "5px",
    marginBottom: "5px",
    border: isSelected ? "2px solid #000" : ""
  };

  return <div style={divStyle} onClick={() => selectColor(color)}></div>;
};

Color.propTypes = {
  color: PropTypes.any,
  isSelected: PropTypes.bool
};

export default Color;
