import React from "react";
import { useTiles } from "../TileHooks";
import "./mosaic.css";

function Mosaic() {
  const { selectedTile, selectedScheme } = useTiles();
  console.log("selected tile: ");
  console.log(selectedTile);

  return (
    <div id="grid-container">
      <svg width="400" height="400" viewBox="0 0 800 800">
        {selectedTile.rotations.map((r, i) => {
          const type = selectedTile.type;
          const transform = calcPosition(i, r, type);
          return (
            <g key={i} transform={transform}>
              {selectedTile.figures.map((f, pathIndex) => {
                return (
                  <path
                    id={pathIndex}
                    key={pathIndex}
                    d={f.d}
                    fill={selectedScheme.colors[pathIndex]}
                  />
                );
              })}
            </g>
          );
        })}
      </svg>
    </div>
  );
}

const calcPosition = (i, r, type = "normal") => {
  let transform = "";
  if (type === "normal")
    transform =
      "translate(" +
      ((i % 4) * 200 + 8) +
      "," +
      (parseInt(i / 4) * 200 + 8) +
      ")";
  else {
    const xInicial = -50;
    const yInicial = -80;
    const numElementosFila = 6;
    const fila = parseInt(i / numElementosFila);
    const columna = i % numElementosFila;
    if (fila % 2 === 0) {
      transform =
        "translate(" +
        (xInicial + columna * 176 - 90 + 8) +
        "," +
        (yInicial + fila * 150) +
        ")";
    } else {
      transform =
        "translate(" +
        (xInicial + columna * 176 + 6) +
        "," +
        (yInicial + fila * 150) +
        ")";
    }
  }
  transform += " rotate(" + r + " 100 100)";
  return transform;
};

export default Mosaic;
