import { PropTypes } from "prop-types";
import React from "react";
import "./colorScheme.css";

function ColorScheme({
  id,
  figures,
  onSelect,
  isSelected = false,
  colorScheme
}) {
  const width = 100;
  const height = 100;
  const styleName = isSelected ? "SelectedColorScheme" : "ColorScheme";
  return (
    <svg
      key={id}
      className={styleName}
      version="1.1"
      width={width}
      height={height}
      viewBox="0 0 200 200"
      xmlns="http://www.w3.org/2000/svg"
      xmlSpace="preserve"
      onClick={() => onSelect()}
    >
      {figures.map((f, i) => {
        return <path key={i} d={f.d} fill={colorScheme.colors[i]} />;
      })}
    </svg>
  );
}

ColorScheme.propTypes = {
  id: PropTypes.number.isRequired,
  figures: PropTypes.array.isRequired,
  colorScheme: PropTypes.any.isRequired,
  isSelected: PropTypes.bool.isRequired,
  onSelect: PropTypes.func.isRequired
};

export default ColorScheme;
