const readColors = () => {
  const tiles = buildTiles();
  let colors = [];

  for (let i = 0; i < tiles.length; i++) {
    const colorSchemes = tiles[i].colorSchemes;
    for (let j = 0; j < colorSchemes.length; j++) {
      const scheme = colorSchemes[j];
      for (let k = 0; k < scheme.colors.length; k++) {
        const color = scheme.colors[k].toLowerCase();
        if (colors.indexOf(color) == -1) colors.push(color);
      }
    }
  }
  return colors;
};

export const buildColors = () => {
  const rawColors = readColors();
  return rawColors;
};
export const buildTiles = () => {
  const rawTiles = readTiles();
  const tiles = rawTiles.map((t, iT) => {
    return {
      id: iT,
      rotations: t.rotations,
      translations: t.translations,
      figures: t.paths.map((p, iF) => {
        const colors = t.colorSchemes[0];
        return {
          id: iF,
          d: p.d,
          color: colors[iF]
        };
      }),
      colorSchemes: t.colorSchemes.map((sc, iC) => {
        return {
          id: iC,
          colors: sc,
          isCustomized: false
        };
      })
    };
  });
  return tiles;
};

