import React from "react";
import { useTiles } from "../TileHooks";
import Color from "./Color";
import "./colorMenu.css";

function ColorMenu() {
  const { selectedColor, colors, selectedScheme } = useTiles();
  console.log("Selected Scheme: ");
  console.log(selectedScheme);
  return (
    <div id="color-menu">
      {colors.map((color, index) => {
        const isSeletedColor = color == selectedColor;
        if (isSeletedColor) {
          console.log("selected color: " + color);
        }
        return <Color key={index} color={color} isSelected={isSeletedColor} />;
      })}
    </div>
  );
}

export default ColorMenu;
