import React from "react";
import { useTiles } from "../TileHooks";
import ColorScheme from "./ColorScheme";
import "./colorSchemeMenu.css";

function ColorSchemeMenu() {
  const { selectedTile, selectScheme, selectedScheme } = useTiles();
  return (
    <div>
      {selectedTile.colorSchemes.map((cs, index) => (
        <ColorScheme
          id={index}
          key={index}
          figures={selectedTile.figures}
          isSelected={selectedScheme.id == index}
          colorScheme={cs}
          stroke={"#000"}
          strokeWidth={0}
          onSelect={() => selectScheme(cs)}
        />
      ))}
    </div>
  );
}

export default ColorSchemeMenu;
