import React from "react";
import { PropTypes } from "prop-types";

function Figure({ width, height, d, color }) {
  const strokeWidth = 3;
  return (
    <svg
      version="1.1"
      width={width}
      height={height}
      viewBox="0 0 200 200"
      xmlns="http://www.w3.org/2000/svg"
      xmlSpace="preserve"
    >
      <path d={d} fill={color} strokeWidth={strokeWidth} stroke="#000" />
    </svg>
  );
}

Figure.propTypes = {
  width: PropTypes.number,
  height: PropTypes.number,
  d: PropTypes.string,
  color: PropTypes.string
};

export default Figure;
