import React from "react";
import Tile from "./Tile";
import PropTypes from "prop-types";
import { useTiles } from "../TileHooks";
import "./tileMenu.css";

function TileMenu() {
  const { selectedTile, selectTile, tiles } = useTiles();
  return (
    <div id="tile-menu">
      <div id="tile-container">
        {tiles.map((tile, index) => {
          return (
            <Tile
              id={tile.id}
              key={index}
              figures={tile.figures}
              colors={tiles[index].colorSchemes[0]}
              stroke={"#000"}
              strokeWidth={0}
              isSelected={selectedTile.id === tile.id}
              onSelect={() => selectTile(tile)}
            />
          );
        })}
      </div>
    </div>
  );
}

TileMenu.propTypes = {
  tiles: PropTypes.array,
  selectedTile: PropTypes.any,
  selectTile: PropTypes.func
};

export default TileMenu;
