import React from "react";
import Figure from "./Figure";
import { useTiles } from "../TileHooks";
import "./figureMenu.css";

function FigureMenu() {
  const { selectedTile, selectedFigure, selectFigure, selectedScheme } =
    useTiles();
  const figures = selectedTile.figures;
  const width = 100;
  const height = 100;
  return (
    <div id="figure-menu">
      <div id="figure-container">
        {figures.map((f, index) => {
          let cssClassName = "figure";
          if (f.id == selectedFigure.id) {
            cssClassName = "selected-figure";
          }
          return (
            <div
              key={index}
              className={cssClassName}
              onClick={() => selectFigure(f)}
            >
              <Figure
                width={width}
                height={height}
                d={f.d}
                color={selectedScheme.colors[index]}
              />
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default FigureMenu;
