import { PropTypes } from "prop-types";
import React from "react";
import "./tile.css";

function Tile({ id, figures, isSelected = false, onSelect = (f) => f }) {
  const width = 100;
  const height = 100;
  const styleName = isSelected ? "SelectedMenuTile" : "MenuTile";
  return (
    <svg
      key={id}
      className={styleName}
      version="1.1"
      width={width}
      height={height}
      viewBox="0 0 200 200"
      xmlns="http://www.w3.org/2000/svg"
      xmlSpace="preserve"
      onClick={() => onSelect(id)}
    >
      {figures.map((f, i) => {
        return <path key={i} d={f.d} fill={f.color} />;
      })}
    </svg>
  );
}

Tile.propTypes = {
  id: PropTypes.number,
  figures: PropTypes.array,
  isSelected: PropTypes.bool,
  onSelect: PropTypes.func
};

export default Tile;
