import React, { createContext, useState, useContext } from "react";
import PropTypes from "prop-types";
import { buildColors, buildTiles } from "./DataReader";

const TileContext = createContext();
export const useTiles = () => useContext(TileContext);

const tiles = buildTiles();
const colors = buildColors();

export function TileProvider({ children }) {
  const getFirstTile = () => {
    return tiles[0];
  };

  const getFirstFigure = () => {
    const firstTile = getFirstTile();
    return firstTile.figures[0];
  };

  const getFirstColor = () => {
    return colors[0];
  };

  const getFirstScheme = () => {
    const firstTile = getFirstTile();
    return firstTile.colorSchemes[0];
  };

  const [selectedTile, setSelectedTile] = useState(getFirstTile());
  const [selectedScheme, setSelectedScheme] = useState(getFirstScheme());
  const [selectedFigure, setSelectedFigure] = useState(getFirstFigure());
  const [selectedColor, setSelectedColor] = useState(getFirstColor());

  const selectTile = (tile) => {
    setSelectedTile(tile);
    const defaultColorScheme = tile.colorSchemes[0];
    setSelectedScheme(defaultColorScheme);
    setSelectedFigure(tile.figures[0]);
    const defaultFigureColor = defaultColorScheme.colors[0];
    setSelectedColor(defaultFigureColor);
  };

  const selectScheme = (scheme) => {
    console.log("selectScheme ");
    console.log(scheme);
    setSelectedScheme(scheme);
    const figure = selectedTile.figures[0];
    setSelectedFigure(figure);
    const figureColor = scheme.colors[figure.id];
    setSelectedColor(figureColor);
  };

  const selectFigure = (figure) => {
    if (selectedFigure.id == figure.id) return;

    setSelectedFigure(figure);
    const figureColor = selectedScheme.colors[figure.id];
    setSelectedColor(figureColor);
  };

  const selectColor = (color) => {
    if (color === selectedColor) return;

    let scheme = selectedScheme;

    let isNewScheme = false;
    if (!selectedScheme.isCustomized) {
      scheme = buildNewScheme(selectedScheme);
      isNewScheme = true;
    }

    const oldColorIndex = selectedScheme.colors.indexOf(selectedColor);
    scheme.colors[oldColorIndex] = color;
    const tile = tiles.find((t) => t.id == selectedTile.id);
    if (isNewScheme) {
      tile.colorSchemes.push(scheme);
      setSelectedScheme(scheme);
    }

    setSelectedScheme(scheme);
    console.log("selectColor----");
    console.log("color: " + color);
    const figure = selectedTile.figures[0];
    console.log("selected figure id: ");
    console.log(figure.id);
    setSelectedFigure(figure);
    const figureColor = scheme.colors[figure.id];
    console.log("Scheme al final");
    console.log(scheme);
    setSelectedColor(figureColor);
  };

  const buildNewScheme = (baseScheme) => {
    const newScheme = cloneColorScheme(baseScheme);
    newScheme.id = selectedTile.colorSchemes.length;
    newScheme.isCustomized = true;
    return newScheme;
  };

  const cloneColorScheme = (scheme) => {
    return JSON.parse(JSON.stringify(scheme));
  };

  TileProvider.propTypes = {
    children: PropTypes.any
  };

  return (
    <TileContext.Provider
      value={{
        tiles,
        selectedTile,
        selectTile,
        selectedScheme,
        selectScheme,
        selectedFigure,
        getFirstFigure,
        selectFigure,
        colors,
        selectedColor,
        selectColor
      }}
    >
      {children}
    </TileContext.Provider>
  );
}
